import api from '../../utils/api.js';
import util from '../../utils/util.js';
export default {
	data() {
		return {
			list: [],
			check_index: 0
		};
	},
	onLoad(options) {
		const that = this;
		api.keeperCover({
			data: {
				id: options.id
			},
			success: function(res) {
				that.list = res.list;
			}
		});
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		clickItem(e) {
			let index = e.currentTarget.dataset.index;
			this.check_index = index;
		},
		submit() {
			uni.navigateTo({
				url: '../edit/edit'
			});
		}
	}
};
